.list{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-family: 'Roboto', sans-serif;
  @media screen and (max-width: 1050px){
   justify-content: center
  }

  @media screen and (max-width: $desktop-breakpoint){
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 2rem;
  }
}

.list-card{ 
  position: relative;
  height: 42rem;
  width: 55vw;
  margin: 4rem; 
  box-shadow: 0 10px 20px rgba($black, 0.3), 0 6px 17px rgba($black, 0.2);
  @media screen and (max-width: $desktop-breakpoint){
    text-align: center;
    margin: 4.3rem 0;
    border-radius: 3rem;
    width: 100%;
    height: 65vh; 
    // padding: 3rem;
  }

  &:hover{
    // -webkit-transform: scale(0.9);  
    // -ms-transform: scale(0.9);  
    // transform: scale(0.92);  
    // -webkit-box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
    // box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
    transition: all 0.3s ease;
    transform: scale(1.03); 
    cursor: pointer;
  }
}



.list-card__body{
  display: grid;
  grid-template-columns:  1.5fr 1fr;
  grid-template-rows: 2fr 1fr;
  grid-template-areas: 
  "img content"
  "data content";
  height: inherit;
  @media screen and (max-width: 1050px){
    grid-template-columns: 1fr 2fr;
   }
  @media screen and(max-width: $desktop-breakpoint){
    display: flex;
    flex-direction: column;
  }
}

.list-card__image img{
  grid-area: img;
  width: 90%;
  top: -3rem;
  left: 3rem;
  height: 40vh;
  margin: 0;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 5px 10px 30px 0px rgba(0,0,0,0.75);
  @media screen and(max-width: $desktop-breakpoint){
    display: none;
  }
  // &:hover{
  //   background: #333;
  //   transform: scale(1.2);  
  //   transition: transform 2s ease-in;
  // }
}

.list-card__content{
  overflow: hidden;
  grid-area: content;
  @media screen and (max-width: 1050px){
    // min-width: ;
   }
  margin: 0.6rem 1rem;
  h3{
    margin:0 1rem 1rem 0;
  }
  padding: 0.5rem;

  
}
.list-card__body-author{
  align-self: start;
  display: flex;
  color: white;
  font-weight: 100;
  padding: .3rem .3rem 1rem .3rem;
  justify-content: flex-start;
  border-bottom: 2px solid #C3C3C3;
  @media screen and (max-width: $desktop-breakpoint){
    margin-top: 1rem;
  }
  img{
    border-radius: 10rem;
    height: 3rem;
    margin-right: 0.5rem;
    @media screen and (max-width: $desktop-breakpoint){
      height: 5rem;   
     }
  }
  h3{
    font-size: $font-size-small;
    margin: .1rem 1rem 0 0 ;
    background: $grey;
    border-radius: 1rem;
    padding: 0.4rem 0.8rem;
    font-weight: 100;
    @media screen and (max-width: $desktop-breakpoint){
      height: 5rem; 
      font-size: $font-size-large;
      padding: 0 1rem;
      padding-top: 1rem;
     }
  }
}
.list-card__body-title{
  color: #4B4B4B;
  font-weight: 700; 
  text-transform: capitalize;
}

.list-card__body-content{
	padding-top : 10px;
	font-size : 0.95rem;
	line-height: 150%;
  color : #4B4B4B;
}

.list-card__content-text{
	text-align: justify;
	padding-top : 10px;
	font-size : $font-size-small;
	line-height: 150%;
	color : #4B4B4B;
}

.list-card__body-date {
  grid-area: data;  
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-left: 4rem;
  margin-bottom: 2rem;
  h5{
    font-size : 5rem;
    color : #C3C3C3;
    margin: 0;
  }
  h6{
    font-size : 2rem;
    color : #C3C3C3;
    margin: 0;
  }

  
  ul{
    display: flex;
    align-items: flex-end;
    justify-content: end;
    margin-bottom: 2rem;
    @media screen and(max-width: $desktop-breakpoint){
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }
  }
  li{
    list-style: none;
    padding-right: 40px;
    color : #7B7B7B;
  } 
  @media screen and(max-width: $desktop-breakpoint){
    display: flex;
    flex-direction: column;
    margin: 0;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
    h5{
      font-size : 3rem;

      color : #C3C3C3;
      margin: 0;
    }
    h6{
      font-size : 2rem;
      color : #C3C3C3;
      margin: 0;
    }

  }
}

.list-card__fab{
  position : absolute;
	right : 50px;
  bottom : -40px;
  @media screen and(max-width:$desktop-breakpoint){
    right : 70px;
    bottom : -50px;
  }
	box-sizing: border-box;
	padding-top: 18px;
	background-color : $grey;
	width : 80px;
	height : 80px;
	color : white;
	text-align : center;
  border-radius : 50%;
	-webkit-box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
	-moz-box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
	box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
}